.container {
  width: 212px;
  position: relative;
}

.imgContainer {
  height: 196px;

  & > div {
    height: 100%;
  }

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.text {
  color: var(--grayscale-gs-secondary-1);
}

.checkbox {
  position: absolute;
  top: 8px;
  right: 8px;
}

.filename {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.extension {
  white-space: nowrap;
  text-overflow: ellipsis;
}