.errorCircle {
  border-radius: 1000px;
  background-color: var(--red);
  width: 56px;
  height: 56px;
}

.controlCircle {
  width: 80px;
  height: 80px;
  border-radius: 1000px;
  background: color-mix(in srgb, var(--grayscale-gs-white) 20%, white 0%);
}

.stopButton {
  background-color: var(--red);

  &:hover {
    background-color: var(--red-hover2);
  }

  &:active {
    background-color: var(--red-pressed2);
  }
}