.container {
  width: 100%;
  position: relative;

  .timer {
    position: absolute;
    top: 24px;
    left: 24px;
    background: rgba(0,0,0,.7);
    border-radius: 24px;
    transition: .3s;
    cursor: default;
    box-shadow: 0 0 8px -4px var(--grayscale-gs-white);
    color: var(--grayscale-gs-white);

    .recordDot {
      background: var(--red);
      width: 10px;
      height: 10px;
      border-radius: 20px;
    }
  }
  .timer:hover {
    background: rgba(0,0,0,1);
    transform: scale(1.06);
  }

  .control {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  .stopButton {
    background: var(--red);
    padding: 27px;
    border-radius: 1000px;
    height: 76px;
    width: 76px;
    position: relative;
  }
  .stopButton::after {
    content: '';
    background: var(--grayscale-gs-white);
    width: 22px;
    height: 22px;
    border-radius: 2px;
    position: absolute;
  }
  .stopButton:hover {
    background: var(--red-hover);
  }

  .stopButton:active {
    background: var(--red-pressed);
  }

  .microphoneButton {
    background: var(--black);
  }

  .microphoneButton:hover {
    background: var(--grayscale-gs-primary);
  }

  .microphoneButton:active {
    background: var(--primary-2);
  }

  .pauseContinueButton {
    background: var(--black);
  }

  .pauseContinueButton:hover {
    background: var(--grayscale-gs-primary);;
  }

  .pauseContinueButton:active {
    background: var(--primary-2);;
  }

  .microphoneButton, .pauseContinueButton {
    border-radius: 300px;
    width: 48px;
    height: 48px;
    & * {
      fill: var(--grayscale-gs-white);
    }
  }

  .stopButton, .microphoneButton, .pauseContinueButton {
    cursor: pointer;
    transition: .3s;
  }

  .stopButton:active, .microphoneButton:active, .pauseContinueButton:active {
    transform: scale(0.95);
  }

  .buttonDisabled {
    filter: grayscale(100%);
    cursor: not-allowed!important;
    opacity: 0.5;
  }
}