$BorderRadiusSizes: ("tiny": 4px, "small": 8px, "medium": 16px, "large": 32px);
$BorderRadiusPlaces: ("Top": (tl, tr), "Right": (tr, br), "Bottom": (br, bl), "Left": (tl, bl), "TopLeft": (tl), "TopRight": (tr), "BottomRight": (br), "BottomLeft": (bl));

@each $name, $size in $BorderRadiusSizes {
  .#{$name}BorderRadius {
    border-radius: $size;
  }
  @each $positionName, $positions in $BorderRadiusPlaces {
    $tl: 0;
    $tr: 0;
    $br: 0;
    $bl: 0;

    @each $position in $positions {
      @if $position == tl {
        $tl: $size;
      }
      @if $position == tr {
        $tr: $size;
      }
      @if $position == br {
        $br: $size;
      }
      @if $position == bl {
        $bl: $size;
      }
    }

    .#{$name}BorderRadius#{$positionName} {
      border-radius: $tl $tr $br $bl;
    }
  }
}

.container {
  overflow: hidden;
}