.container {
  padding: 5px;
  margin: 2px;
  transition: .3s;
  border-radius: 6px;
  cursor: pointer;

  &:hover {
    background: rgba(112, 112, 112, 0.2);
  }

  .hr {
    width: 70px;
  }

  .light {
    background: white;
  }

  .dark {
    background: black;
  }

  .selected {
    box-shadow: 0 0 4px 1px var(--accent-color-1);
  }
}